<template>
  <div>
    <h1>{{ animal.name }} ({{ this.$route.params.species }})</h1>
    <p>{{ animal.age }} years old</p>
    <p>breed ({{ animal.breed }})</p>
  </div>
</template>

<script>
import { mapState } from "vuex"

export default {
  name: "Pet",
  data () {
    return {
      animal: {}
    }
  },
  computed: {
    ...mapState([
      "cats",
      "dogs"
    ])
  },
  mounted () {
    const animal = this[this.$route.params.species][this.$route.params.id]
    this.animal = animal
  }
}
</script>

